import request from '@/request/http';

// 默认地址
export function queryDefaultReceiveArea(query) {
    return request({
        url: '/ssm/weChat/queryDefaultReceiveArea',
        method: 'get',
        params: query
    })
}
// 所有地址
export function queryReceiveArea(query) {
    return request({
        url: '/ssm/user/addressAdministration/chooseReceiverAddress',
        method: 'get',
        params: query
    })
}
// 新增地址
export function addReceiverAddress(query) {
    return request({
        url: '/ssm/user/addressAdministration/addReceiverAddress',
        method: 'get',
        params: query
    })
}

// 修改地址
export function updateReceiverAddress(query) {
    return request({
        url: '/ssm/user/addressAdministration/updateReceiverAddress',
        method: 'get',
        params: query
    })
}

// 地址联动
export function queryAreaList(query) {
    return request({
        url: '/ssm/user/area/list',
        method: 'get',
        params: query
    })
}

// 地址详情
export function receiverAddressDetail(query) {
    return request({
        url: '/ssm/user/addressAdministration/receiverAddressDetail',
        method: 'get',
        params: query
    })
}

// 删除地址
export function deleteReceiverAddress(query) {
    return request({
        url: '/ssm/user/addressAdministration/deleteReceiverAddress',
        method: 'get',
        params: query
    })
}


